import styled from "styled-components";
import {motion, AnimatePresence, useScroll} from "framer-motion";
import {useNavigate} from "react-router-dom";
import {IGetMovieDetail} from "../../api";
import {makeImagePath} from "../../utils";

const Overlay = styled(motion.div)`
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
`;
const BigMovie = styled(motion.div)`
  position: fixed;
  width: 50vw;
  height: 80vh;
  left: 0;
  right: 0;
  margin: 0 auto;
  background-color: ${(props) => props.theme.black.lighter};
  border-radius: 15px;
  overflow: hidden;
  color: ${(props) => props.theme.white.lighter};
`;
const BigCover = styled.div`
  width: 100%;
  background-size: cover;
  background-position: center center;
  height: 400px;
`;
const BigTitle = styled.h3`
  font-size: 35px;
  padding: 30px;
  position: relative;
  top: -90px;
`;
const BigOverview = styled.p`
  padding: 30px;
  position: relative;
  top: -110px;
`;

interface IDetailMovie {
  detailData?: IGetMovieDetail;
}
function DetailMovie({detailData}: IDetailMovie) {
  const navigate = useNavigate();
  const {scrollY} = useScroll();
  const onOverlayClick = () => {
    navigate("/");
  };

  return (
    <>
      <AnimatePresence>
        <>
          <Overlay
            onClick={onOverlayClick}
            animate={{opacity: 1}}
            exit={{opacity: 0}}
          />
          <BigMovie
            style={{top: scrollY.get() + 100}}
            layoutId={detailData?.id + ""}
          >
            {detailData && (
              <>
                <BigCover
                  style={{
                    backgroundImage: `linear-gradient(to top, black, transparent), url(
                  ${makeImagePath(detailData.backdrop_path, "w500")}
                )`,
                  }}
                />
                <BigTitle>{detailData.title}</BigTitle>
                <BigOverview>{detailData.overview}</BigOverview>
              </>
            )}
          </BigMovie>
        </>
      </AnimatePresence>
    </>
  );
}

export default DetailMovie;
